.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.chart-bar__inner {
  width: 100%;
  height: 100%;
  border: 1px solid #313131;
  border-radius: 0.7rem;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.chart-bar__label {
  font-weight: bold;
  font-size: 1rem;
  text-align: center;
}
.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
  display: flex;
}
